* {
  box-sizing: border-box;
}

body {
  padding: 15px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  background: url('http://i.stack.imgur.com/jGlzr.png') no-repeat 0 0 scroll;
    background-color: #fafafa;
    height: 100%;
    width: 100%;
  color: #212121;
  line-height: 1.5;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
}

#search-box:focus {
border-color: #2196f3;
}


span {
  font-weight: 700;
  margin-right: 5px;
}

.country-list__item {
  display: flex;
  align-items: center;
}


.country-list__image {
width: 50px;
  height: 30px;
}

.country-list__title {
  margin-left: 10px;
  font-size: 16px;
}

.country-list__title--one {
  margin-left: 10px;
  font-weight: 700;
  font-size: 40px;
}